<template>
    <div id="app">
      <el-card shadow="hover">
        <span class="title">上级项目详情</span>
    <div>
    <label for="" style="width:20%;vertical-align: middle;">项&nbsp;&nbsp;&nbsp;目&nbsp;&nbsp;&nbsp;&nbsp;编&nbsp;&nbsp;&nbsp;号:</label>
    <el-input
    disabled
    style="width:79%;vertical-align: middle;"
  type="textarea"
  autosize
  placeholder="请输入内容"
  v-model="xmbh">
</el-input>
</div>

 <div class="mar-top">
    <label for="" style="width:20%;vertical-align: middle;">申&nbsp;&nbsp;&nbsp;请&nbsp;&nbsp;&nbsp;&nbsp;单&nbsp;&nbsp;&nbsp;位:</label>
    <el-input
    disabled
    style="width:79%;vertical-align: middle;"
  type="textarea"
  autosize
  placeholder="请输入内容"
  v-model="sqdw">
</el-input>
</div>

 <div class="mar-top">
    <label for="" style="width:20%;vertical-align: middle;">是否是采购项目:</label>
    <el-input
    disabled
    style="width:79%;vertical-align: middle;"
  type="textarea"
  autosize
  placeholder="请输入内容"
  v-model="sfscgxm">
</el-input>
</div>

<div class="mar-top">
    <label for="" style="width:20%;vertical-align: middle;">项&nbsp;&nbsp;&nbsp;目&nbsp;&nbsp;&nbsp;&nbsp;名&nbsp;&nbsp;&nbsp;称:</label>
    <el-input
    disabled
    style="width:79%;vertical-align: middle;"
  type="textarea"
  autosize
  placeholder="请输入内容"
  v-model="xmmc">
</el-input>
</div>

<div class="mar-top">
    <label for="" style="width:20%;vertical-align: middle;">支&nbsp;队&nbsp;领&nbsp;导意见:</label>
    <el-input
    disabled
    style="width:79%;vertical-align: middle;"
  type="textarea"
  autosize
  placeholder="请输入内容"
  v-model="zdldyj">
</el-input>
</div>

<div class="mar-top">
    <label for="" style="width:20%;vertical-align: middle;">支队项目办意见:</label>
    <el-input
    disabled
    style="width:79%;vertical-align: middle;"
  type="textarea"
  autosize
  placeholder="请输入内容"
  v-model="zdxmbyj">
</el-input>
</div>

<div class="mar-top">
    <label for="" style="width:20%;vertical-align: middle;">建 设 单 位意见: </label>
    <el-input
    disabled
    style="width:79%;vertical-align: middle;"
  type="textarea"
  autosize
  placeholder="请输入内容"
  v-model="jsdwyj">
</el-input>
</div>

<div class="mar-top">
    <label for="" style="width:20%;vertical-align: middle;">内&nbsp;&nbsp;&nbsp;容&nbsp;&nbsp;&nbsp;摘&nbsp;&nbsp;&nbsp;&nbsp;要: </label>
    <el-input
    disabled
    style="width:79%;vertical-align: middle;"
  type="textarea"
  autosize
  placeholder="请输入内容"
  v-model="nrzy">
</el-input>
</div>

<div class="mar-top">
    <label for="" style="width:20%;vertical-align: middle;">预&nbsp;&nbsp;&nbsp;算&nbsp;&nbsp;&nbsp;价&nbsp;&nbsp;&nbsp;&nbsp;格: </label>
    <el-input
    disabled
    style="width:79%;vertical-align: middle;"
  type="textarea"
  autosize
  placeholder="请输入内容"
  v-model="ysjg">
</el-input>
</div>

<div class="mar-top">
    <label for="" style="width:20%;vertical-align: middle;">立&nbsp;&nbsp;&nbsp;项&nbsp;&nbsp;&nbsp;依&nbsp;&nbsp;&nbsp;&nbsp;据: </label>
    <el-input
    disabled
    style="width:79%;vertical-align: middle;"
  type="textarea"
  autosize
  placeholder="请输入内容"
  v-model="lxyj">
</el-input>
</div>

<div class="mar-top">
    <label for="" style="width:20%;vertical-align: middle;">承&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;办&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;人: </label>
    <el-input
    disabled
    style="width:79%;vertical-align: middle;"
  type="textarea"
  autosize
  placeholder="请输入内容"
  v-model="cbr">
</el-input>
</div>

<div class="mar-top">
    <label for="" style="width:20%;vertical-align: middle;">单&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;位: </label>
    <el-input
    disabled
    style="width:79%;vertical-align: middle;"
  type="textarea"
  autosize
  placeholder="请输入内容"
  v-model="dw">
</el-input>
</div>

<div class="mar-top">
    <label for="" style="width:20%;vertical-align: middle;">开&nbsp;&nbsp;&nbsp;始&nbsp;&nbsp;&nbsp;日&nbsp;&nbsp;&nbsp;&nbsp;期: </label>
    <el-input
    disabled
    style="width:79%;vertical-align: middle;"
  type="textarea"
  autosize
  placeholder="请输入内容"
  v-model="ksrq">
</el-input>
</div>
<div class="mar-top">
    <label for="" style="width:20%;vertical-align: middle;">立&nbsp;&nbsp;&nbsp;项&nbsp;&nbsp;&nbsp;附&nbsp;&nbsp;&nbsp;&nbsp;件: </label>
    <el-input
    disabled
    style="width:79%;vertical-align: middle;"
  type="textarea"
  autosize
  placeholder="请输入内容"
  v-model="lxfj">
</el-input>
</div>
</el-card>
    </div>
</template>
<script>
export default {
  name:'addLeveXq',
  props:["id"],
    data(){
      return{
        xmbh:'1',
        sqdw:'2',
        sfscgxm:'3',
        xmmc:'4',
        zdldyj:'5',
        zdxmbyj:'6',
        jsdwyj:'7',
        nrzy:'8',
        ysjg:'9',
        lxyj:'10',
        cbr:'11',
        dw:'12',
        ksrq:'13',
        lxfj:'14',
      }
    },
    methods:{
      axios(){
        this.$axios.get(`/project/getOneProjectInfo?sysId=${this.id}`).then(res=>{
          console.log(res)
          var data = res.data.data.shangJiProject;
          this.xmbh = data.projectNum;
          this.sqdw = data.projectForUnit;
          this.sfscgxm = data.isCaiGou;
          this.xmmc = data.projectName;
          this.zdldyj = data.leaderOpinion;
          this.zdxmbyj = data.projectDepartmentOpinion;
          this.jsdwyj = data.constructionOpinion;
          this.nrzy = data.projectContent;
          this.ysjg = data.projectBudgetPrice;
          this.lxyj = data.projectBasis;
          this.cbr = data.promoterId;
          this.dw = data.projectUnitId;
          this.ksrq = data.startTime;
          this.lxfj = data.projectAttachment;
        })
      }
    },
    mounted(){
      this.axios();
    },
    watch:{
      id(){
        this.axios();
      }
    }
}
</script>
<style scoped>
    .mar-top{
      margin-top: 15px;
    }
    #app >>> .el-textarea.is-disabled .el-textarea__inner{
      background-color: white;
    }
    #app >>> .el-card__body{
      padding-top: 0;
    }
    .title{
      text-align: center;
      margin: 15px 0;
      display: block;
      font-size: 18px;
      font-weight: bold;
    }
</style>



